import { Component, h, State } from "@stencil/core"
import throttle from "lodash-es/throttle"

@Component({
  tag: "sdx-itunes-autocomplete",
  shadow: true,
})
export class ItunesAutocomplete {
  private fetchThrottled: (keyword: string) => void

  @State() private artists: string[] = []
  @State() private loading: boolean = false

  constructor() {
    this.fetchThrottled = throttle(this.fetch, 500)
  }

  private onChange(keyword: string) {
    // Show loading spinner
    this.loading = true

    this.fetchThrottled(keyword)
  }

  private fetch(keyword: string): void {
    // Clear results from last search (if any)
    this.artists = []

    // Ensure keyword length
    if (keyword.length < 3) {
      this.loading = false
      return
    }

    // Request data
    fetch(
      `https://itunes.apple.com/search?term=${encodeURI(
        keyword
      )}&entity=musicArtist&limit=10`,
      {
        method: "post",
      }
    )
      .then((payload) => payload.json())
      .then(
        (payload) =>
          (this.artists = payload.results.map(
            (result: any) => result.artistName
          ))
      )
      .then(() =>
        setTimeout(() => {
          this.loading = false
        }, 100)
      ) // timeout because there's a gap between data arrival and the select rendering
  }

  public render() {
    return (
      <sdx-select
        label="Your favourite artist on iTunes"
        placeholder="Search artists..."
        onInput={(e) => {
          const selection = (e.target as HTMLSdxSelectElement).value
          this.onChange(selection[0])
        }}
        keyboardBehavior="autocomplete"
        filterFunction={
          () => true /* iTunes already filters, bypass local filter */
        }
        loading={this.loading}
      >
        {this.artists.map((artist) => (
          <sdx-select-option>{artist}</sdx-select-option>
        ))}
      </sdx-select>
    )
  }
}
